import * as React from "react";
import { createRoot } from "react-dom/client";
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import App from "./App";
import AllAbout from "./About/AllAbout";
import AllServices from "./Services/AllServices";
import AllBlog from "./Blog/AllBlog";
import AllContact from "./Contact/AllContact";
import AllCart from "./Cart/AllCart";
import AllApi from "./Products/AllApi";
import AllCheckOut from "./Checkout/AllCheckOut";
import AllThankU from "./ThankU/AllThankU";
import Register from "./Registeration/Register";

const router = createBrowserRouter([
  {
    path: "/",
    element: <App></App>,
  },
  {
    path: "about",
    element: <AllAbout/>,
  },
  {
    path: "products",
    element: <AllApi/>,
  },
  {
    path: "services",
    element: <AllServices/>,
  },
  {
    path: "blog",
    element: <AllBlog/>,
  },
  {
    path: "contact",
    element: <AllContact/>,
  },
  {
    path: "cart",
    element: <AllCart/>,
  },
  {
    path: "checkout", 
    element: <AllCheckOut/>
  },
  {
    path: "thanku", 
    element: <AllThankU/>
  },
  {
    path: "Register", 
    element: <Register/>
  }
]);

createRoot(document.getElementById("root")).render(
  <RouterProvider router={router} />
);
