import React from 'react'
import Navbar from '../Navbar'
import Footer from '../Footer'
import Hero from './Hero'
import { CartProvider, useCart } from 'react-use-cart'

import { NavLink, useNavigate } from "react-router-dom";

function CartDisplay() {
  const shipping = 20;
  const { isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem, cartTotal } = useCart();
  const navigate = useNavigate();   

  const AllCheckOut = () => {
    navigate("/checkout");      
  };

  if (isEmpty) return <p className="text-center h3 ">Your cart is empty</p>;

  return (
    <div>
      <section className="h-100 h-custom" style={{ backgroundColor: "#eff2f1" }}>
        <div className="container py-5 h-100">
          <div className="row d-flex justify-content-center align-items-center h-100">
            <div className="col-12">
              <div className="card card-registration card-registration-2" style={{ borderRadius: "15px" }}>
                <div className="card-body p-0">
                  <div className="row g-0">
                    {/* Cart Items */}
                    <div className="col-lg-8">
                      <div className="p-5">
                        <div className="d-flex justify-content-between align-items-center mb-5">
                          <h1 className="fw-bold mb-0">Shopping Cart ({totalUniqueItems})</h1>
                          <h6 className="mb-0 text-muted">{totalUniqueItems} items</h6>
                        </div>
                        <hr className="my-4" />
                        
                        {items.map((item) => (
                          <div className="row mb-4 d-flex justify-content-between align-items-center" key={item.id}>
                            <div className="col-md-2 col-lg-2 col-xl-2">
                              <img src={item.images} className="img-fluid rounded-3" alt={item.title} />
                            </div>
                            <div className="col-md-3 col-lg-3 col-xl-3">
                              <h6 className="mb-0">{item.title}</h6>
                            </div>
                            <div className="col-md-3 col-lg-3 col-xl-2 d-flex">
                              <button className="btn btn-link px-2" onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>
                                <i className="fas fa-minus"></i>
                              </button>

                              <span className="mx-auto my-auto">{item.quantity}</span>

                              <button className="btn btn-link px-2" onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>
                                <i className="fas fa-plus"></i>
                              </button>
                            </div>
                            <div className="col-md-3 col-lg-2 col-xl-2 offset-lg-1">
                              <h6 className="mb-0">€ {item.price}</h6>
                            </div>
                            <div className="col-md-1 col-lg-1 col-xl-1 text-end">
                              <button className="btn text-light" onClick={() => removeItem(item.id)}>
                                <i className="fas fa-times"></i>
                              </button>
                            </div>
                          </div>
                        ))}
                        <hr className="my-4" />
                        <div className="pt-5">
                          <h6 className="mb-0">
                            <NavLink to="/products" className="text-body">
                              <i className="fas fa-long-arrow-alt-left me-2"></i>
                              Back to shop
                            </NavLink>
                          </h6>
                        </div>
                      </div>
                    </div>
                    
                    {/* Summary Section */}
                    <div className="col-lg-4 bg-body-tertiary">
                      <div className="p-5">
                        <h3 className="fw-bold mb-5 mt-2 pt-1">Summary</h3>
                        <hr className="my-4" />

                        <div className="mb-4">
                          {/* Items Total */}
                          <div className="d-flex justify-content-between">
                            <h5 className="text-uppercase">Items</h5>
                            <h5>
                              €{" "}
                              {items
                                .reduce((total, item) => total + item.quantity * item.price, 0)
                                .toFixed(2)}
                            </h5>
                          </div>
                          <hr className="text-muted bg-light" />
                          
                          {/* Discount */}
                          <div className="d-flex justify-content-between">
                            <h5 className="text-uppercase">Discount</h5>
                            <h5>
                              €{" "}
                              {items
                                .reduce(
                                  (total, item) =>
                                    total + (item.price * item.quantity * item.discountPercentage) / 100,
                                  0
                                )
                                .toFixed(2)}
                            </h5>
                          </div>
                          <hr className="text-muted bg-light" />
                                
                          {/* Shipping */}
                          <div className="d-flex justify-content-between">
                            <h5 className="text-uppercase">Shipping</h5>
                            <h5>€ {shipping.toFixed(2)}</h5>
                          </div>
                          <hr className="text-muted bg-light" />

                          {/* Total */}
                          <div className="d-flex justify-content-between">
                            <h5 className="text-uppercase">Total</h5>
                            <h5>
                              €{" "}
                              {(
                                items.reduce(
                                  (total, item) =>
                                    total + item.quantity * item.price - 
                                    (item.price * item.quantity * item.discountPercentage) / 100,
                                  0
                                ) + shipping
                              ).toFixed(2)}
                            </h5>
                          </div>
                        </div>
                        <hr className="text-muted bg-light" />


                        {/* Checkout Button */}
                        <button type="button" className="btn btn-dark btn-block btn-lg btn-check-out" onClick={AllCheckOut}>
                          Checkout
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}


function AllCart() {
  return (
    <CartProvider>
      <Navbar/>
      <Hero/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <CartDisplay/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <br/>
      <Footer/>
    </CartProvider>
  )
}
export default AllCart