import React from "react";
import Navbar from "../Navbar";
import Footer from "../Footer";

function Register() {
  return (
    <div>
      <Navbar />
      <br />
      <br />
      <div>
        {/* Pills navs */}
        <ul
          className="nav nav-pills nav-justified mb-3 w-50 mx-auto"
          id="ex1"
          role="tablist"
        >
          <li className="nav-item" role="presentation">
            <button
              className="nav-link active bg-dark text-white w-75 mx-auto"
              id="tab-login"
              data-bs-toggle="pill"
              data-bs-target="#pills-login"
              type="button"
              role="tab"
              aria-controls="pills-login"
              aria-selected="true"
            >
              Login
            </button>
          </li>
          <li className="nav-item" role="presentation">
            <button
              className="nav-link bg-dark text-white w-75 mx-auto"
              id="tab-register"
              data-bs-toggle="pill"
              data-bs-target="#pills-register"
              type="button"
              role="tab"
              aria-controls="pills-register"
              aria-selected="false"
            >
              Register
            </button>
          </li>
        </ul>

        {/* Pills content */}
        <div className="tab-content">
          {/* Login Tab */}
          <div
            className="tab-pane fade show active"
            id="pills-login"
            role="tabpanel"
            aria-labelledby="tab-login"
          >
            <form className="mx-auto w-50">
              <div className="text-center mb-3">
                <p>Sign in with:</p>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-facebook-f"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-google"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-twitter"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-github"></i>
                </button>
              </div>

              <p className="text-center">or:</p>

              {/* Email input */}
              <div className="mb-4">
                <label htmlFor="loginName" className="form-label">
                  Email or username
                </label>
                <input type="email" id="loginName" className="form-control" />
              </div>

              {/* Password input */}
              <div className="mb-4">
                <label htmlFor="loginPassword" className="form-label">
                  Password
                </label>
                <input
                  type="password"
                  id="loginPassword"
                  className="form-control"
                />
              </div>

              {/* Remember Me */}
              <div className="row mb-4">
                <div className="col-md-6 d-flex justify-content-center">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      id="loginCheck"
                      defaultChecked
                    />
                    <label className="form-check-label" htmlFor="loginCheck">
                      Remember me
                    </label>
                  </div>
                </div>
                <div className="col-md-6 d-flex justify-content-center">
                  <a href="#!">Forgot password?</a>
                </div>
              </div>

              {/* Submit button */}
              <button type="submit" className="btn btn-primary btn-block mb-4">
                Sign in
              </button>

              {/* Register link */}
              <div className="text-center">
                <p>
                  Not a member? <a href="#!">Register</a>
                </p>
              </div>
            </form>
          </div>

          {/* Register Tab */}
          <div
            className="tab-pane fade"
            id="pills-register"
            role="tabpanel"
            aria-labelledby="tab-register"
          >
            <form className="mx-auto w-50">
              <div className="text-center mb-3">
                <p>Sign up with:</p>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-facebook-f"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-google"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-twitter"></i>
                </button>
                <button type="button" className="btn btn-primary mx-1">
                  <i className="fab fa-github"></i>
                </button>
              </div>

              <p className="text-center">or:</p>

              {/* Name input */}
              <div className="mb-4">
                <label htmlFor="registerName" className="form-label">
                  Name
                </label>
                <input type="text" id="registerName" className="form-control" />
              </div>

              {/* Username input */}
              <div className="mb-4">
                <label htmlFor="registerUsername" className="form-label">
                  Username
                </label>
                <input
                  type="text"
                  id="registerUsername"
                  className="form-control"
                />
              </div>

              {/* Email input */}
              <div className="mb-4">
                <label htmlFor="registerEmail" className="form-label">
                  Email
                </label>
                <input
                  type="email"
                  id="registerEmail"
                  className="form-control"
                />
              </div>

              {/* Password input */}
              <div className="mb-4">
                <label htmlFor="registerPassword" className="form-label">
                  Password
                </label>
                <input
                  type="password"
                  id="registerPassword"
                  className="form-control"
                />
              </div>

              {/* Repeat Password input */}
              <div className="mb-4">
                <label htmlFor="registerRepeatPassword" className="form-label">
                  Repeat password
                </label>
                <input
                  type="password"
                  id="registerRepeatPassword"
                  className="form-control"
                />
              </div>

              {/* Checkbox */}
              <div className="form-check d-flex justify-content-center mb-4">
                <input
                  className="form-check-input me-2"
                  type="checkbox"
                  id="registerCheck"
                  defaultChecked
                />
                <label className="form-check-label" htmlFor="registerCheck">
                  I have read and agree to the terms
                </label>
              </div>

              {/* Submit button */}
              <button type="submit" className="btn btn-primary btn-block mb-3">
                Sign up
              </button>
            </form>
          </div>
        </div>
      </div>
      <br />
      <br />
      <Footer />
    </div>
  );
}

export default Register;
