import React, { Component } from "react";
import Navbar from "../Navbar";
import Hero from "./Hero";
import Testimonial from "../Testimonial";
import Footer from "../Footer";
import { CartProvider, useCart } from "react-use-cart"; // Import useCart hook
import ApiData from "./ApiData"; // Import the ApiData component

// Functional component for rendering products with Add to Cart functionality
function ProductList() {
  const { addItem } = useCart(); // Get the addItem function from useCart hook

  return (
    <div className="untree_co-section product-section before-footer-section">
      <div className="container">
        <div className="row">
          {ApiData.map((product) => (
            <div className="col-12 col-md-4 col-lg-3 mb-5" key={product.id}>
              <div className="product-item">
                <img
                  src={product.images}
                  alt={product.name}
                  className="img-fluid product-thumbnail"
                />
                <h3 className="product-title">{product.title}</h3>
                <h3>
                  <span>Rating: {product.rating}</span>
                </h3>
                <h3>
                  Stock: <span>{product.stock}</span>
                </h3>
                <strong className="product-price">${product.price}</strong>
                <span
                  className="icon-cross"
                  onClick={() => addItem(product)} // Call addItem when clicked
                  style={{ cursor: "pointer" }}
                >
                  <img src="images/cross.svg" className="img-fluid" />
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

  
class AllApi extends Component {
  render() {
    return (
      <CartProvider>
        <Navbar />
        <Hero />
        <ProductList /> {/* Use the ProductList functional component */}
        <Testimonial />
        <Footer />
      </CartProvider>
    );
  }
}

export default AllApi;
